// general

* {
  font-family: "Montserrat", sans-serif;
}

body {
  line-height: 1.7;
  background: #f4f4f4;
}

html {
  scroll-behavior: smooth;
}

a {
  color: $purple;

  &:hover {
    color: $primary;
  }
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2 {
  font-weight: 700;
  margin-top: 35px;
  scroll-margin-top: 65px;
}

h3,
h4,
h5,
h6 {
  font-weight: 700;
  margin-top: 15px;
  scroll-margin-top: 65px;
}

table tbody tr:nth-child(odd) {
  background-color: #eaeaea;
}

table {
  margin-bottom: 20px;
}

td,
th {
  padding: 2px 10px;
}

.media {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;

  iframe {
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute;
  }
}

.spanborder {
  //   border-bottom: 1px solid #e8f3ec;
  margin-bottom: 2rem;
}

.spanborder span {
  border-bottom: 1px solid rgba(0, 0, 0, 0.44);
  display: inline-block;
  padding-bottom: 20px;
  margin-bottom: -1px;
}

// link into author page
.col-md-9 {
  a {
    color: #212529;

    &:hover {
      color: darken($primary, 24%);
    }
  }
}

.site-content {
  padding-top: 50px;
}

.bigradius {
  border-radius: 40px;
}

.mb-neg-30 {
  margin-bottom: -30px;
}

.btn {
  min-width: 180px;
}

blockquote {
  font-family: Times New Roman;
  font-style: italic;
  font-size: 1.25em;
  position: relative;
  border-left: 1px solid $dark;
  margin-left: 30px;
  padding-left: 15px;

  cite {
    font-weight: 700;
  }
}

cite {
  font-weight: bold;
  font-style: italic;
  border-left: 1px solid $dark;
  padding-left: 5px;
}

// animation
.nav-icon,
.hamburger-bar,
.hamburger-bar:before,
.hamburger-bar:after,
#menu,
body,
a,
a:hover,
body.active-side,
.site-content,
.show-search,
.show-search:hover,
.title,
.title:hover,
.card,
.card:hover,
img,
img:hover {
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

@keyframes fade-down {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

@keyframes fade-up {
  0% {
    opacity: 0;
    transform: translateY(20%);
  }

  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

.featured-image,
.firstpage .blog-grid-item:nth-child(1),
.firstpage .blog-grid-item:nth-child(2),
.firstpage .blog-grid-item:nth-child(3) {
  animation: fade-up 0.7s ease-out forwards running;
  border-radius: 15px;
}

