//reading post
.readingpost {
  font-size: 0.813em;
  color: #444;
  font-weight: 450;
  top: 20px;
  margin: 0px 0px 0px 0px;
  padding: 0px 20px 10px 20px;
  position: relative;
  background-color: #f4f4f4;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  z-index: 89;
  i {
    font-size: 0.875em;
  }
  .reading-time {
    display: inline-block;
  }
  p {
    margin: initial;
  }
}

// blog grid
.blog-grid-container {
  width: auto;
  margin: 0 auto 50px auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-gap: 5px;
  align-items: stretch;
  grid-template-areas: "post-grid post-grid right-bar";
}

.post-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 5px 5px;
  grid-template-areas: ". .";
  grid-area: post-grid;
}

.right-bar {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: 5px 0px;
  grid-template-areas:
    "publicity"
    "featured";
  justify-items: end;
  justify-content: center;
  grid-area: right-bar;
}

.publicity {
  justify-items: stretch;
  width: auto;
  grid-area: publicity;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  padding-top: 45px;

  img {
    margin-top: 10px;
  }
}

.featured {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  grid-area: featured;
}

.blog-grid-item {
  color: #333;
  padding: 0;
  display: flex;
  align-items: center;

  .author-thumb {
    width: 35px;
    height: auto;
    border-radius: 50%;
    margin-right: 4px;
  }
  .author-meta {
    .post-name {
      font-size: 0.75em;
    }
  }

  .card {
    border: 0;

    &:hover {
      .card-title a {
        background-size: 100% 100%;
      }
    }
  }

  .card-title {
    font-weight: 700;
    font-size: 1.25em;
    line-height: 1.4;

    a {
      background-image: linear-gradient(180deg, transparent 89%, $primary 0);
      background-repeat: no-repeat;
      background-size: 0 100%;
      transition: background-size 0.6s ease;
      position: relative;

      &:hover {
        text-decoration: none;
      }
    }
  }

  .card-text {
    font-weight: 400;
    font-size: 0.75em;
    line-height: 1.4;
    margin-top: 5px;
  }

  .card-footer {
    font-size: 0.875em;
    border-top: 0;
    padding: 1rem 1.25rem;

    a {
      color: inherit;
      font-weight: 700;
    }
  }
  .post-date {
    float: right;
    margin-top: 8px;
  }

  .img-thumb {
    object-fit: cover;
    height: auto;
    width: 100%;
    object-position: center;
  }
}

//pagination
.pagination {
  justify-content: center;

  a {
    background: rgba(0, 0, 0, 0.05);
    color: #000;
    cursor: pointer;

    &:hover {
      background: $primary;
      text-decoration: none;
    }
  }

  a,
  span {
    width: 40px;
    height: 40px;
    display: inline-block;
    border-radius: 50%;
    text-align: center;
    line-height: 40px;
  }

  li.selected a {
    cursor: auto;
    color: #666;
    background: none;
  }

  span {
    border: 0;
  }
}

//newsletter
.newsletter {
  h4 {
    line-height: 1.3;
  }

  margin-top: 120px;

  img.newsletter-logo {
    max-height: 70px;
  }

  input {
    border: 1px solid transparent;
  }

  .mc-field-group {
    border: 1px solid;
    border-radius: 2px;
    display: inline-block;

    input[type="email"] {
      padding: 4px 10px;
      min-width: 250px;
    }

    input[type="submit"] {
      color: #222;
      padding: 4px 20px;
      margin-left: -5px;
      background: $primary;
      border-left: 1px solid;
      font-weight: 700;
    }
  }

  form {
    margin-top: 30px;
  }
}

