.c-rating button {
  cursor: none;
}

.spoiler {
  color: transparent;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  transition: all 0.4s;
  cursor: pointer;
  position: relative;

  &:after {
    position: absolute;
    opacity: 0;
    content: "Click to reveal spoiler";
    top: 45%;
    left: calc(50% - 75px);
    text-shadow: none;
    background: #222;
    color: #fff;
    display: inline-block;
    font-size: 0.813em;
    line-height: 1;
    padding: 2px 3px;
    width: 150px;
    font-family: Arial;
    text-align: center;
    border-radius: 3px;
    transition: all 0.4s;
  }

  &:hover:after {
    opacity: 1;
  }
}

.comments {
  margin-top: 30px;
  margin-bottom: 60px;

  #comments {
    display: none;
    margin-top: 60px;

    &.comments--show {
      display: block;
    }
  }
}

.related-posts {
  margin-top: 120px;

  .badge {
    font-size: 0.938em;
    padding: 5px 7px;
    display: inline-block;
    margin: 3px 3px;
  }

  .d-flex {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

// badge

.badge-primary {
  background-color: $danger;
  color: #fff;
