  font-size: 0.438em;
  bottom: 9px;
  left: 5px;
}

.badge-light {
  color: #212529;
  border-radius: 20px;
  padding-top: 6px;
  position: relative;
  top: 9px;
  left: 5px;
  transform: translateY(-50%);
}

.badge-light.categories {
  background-color: $badge-yellow;
}

.badge-light.tags {
  background-color: $badge-blue;
}

.badge-light.posts {
  background-color: $badge-pink;
}

//share
.share {
  position: fixed;
  left: 15px;
  bottom: 40%;
  z-index: 1026;
  text-align: center;

  ul {
    list-style: none;
    padding-left: 0;

    li {
      margin: 15px 0;

      a {
        color: $dark;
      }
    }
  }
}

//search
.show-search {
  cursor: pointer;

  &:hover {
    color: darken($primary, 20%);
  }
}

.bd-search {
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s linear;

  &.search--show {
    visibility: visible;
    opacity: 1;
    transition: opacity 0.5s linear;
  }
}

.wrap-search {
  position: fixed;
  left: 30px;
  bottom: 25px;
  z-index: 1026;
}

.modal-open .modal {
  background: rgba(0, 0, 0, 0.3);
}

.lunrsearchresult {
  .title {
    color: #000;
    font-weight: 700;
    line-height: 1.2;
    display: block;
    font-size: 1.25em;
    margin-bottom: 5px;

    &:hover {
      color: darken($primary, 20%);
    }
  }

  span.body {
    color: $gray-500;
    line-height: 1.7;
    display: block;
    margin-bottom: 10px;
  }

  span.description {
    color: $gray-700;
    line-height: 1.7;
    display: block;
    margin-bottom: 10px;
    font-size: 0.875em;
  }

  span.tags {
    color: $gray-500;
    line-height: 1.7;
    display: block;
    margin-bottom: 10px;
    font-size: 0.75em;

    &:hover {
      color: darken($primary, 20%);
    }
  }

  a {
    &:hover {
      text-decoration: none;
    }
  }

  span.url {
    display: none;
  }
}

body.active-side {
  .bd-search {
    visibility: visible;
    opacity: 1;
    transition: opacity 0.5s linear;
  }
}

//responsive masonry
@media (min-width: 360px) {
  .card-columns {
    column-count: 2;
  }
}

@media (min-width: 576px) {
  .card-columns {
    column-count: 2;
  }
}

@media (min-width: 768px) {
  .card-columns {
    column-count: 3;
  }
}

@media (min-width: 992px) {
  .card-columns {
    column-count: 3;
  }
}

@media (min-width: 1200px) {
  .card-columns {
    column-count: 3;
  }
}

//responsive tweaks
@media (min-width: 768px) {
  .container,
  .container-sm,
  .container-md,
  .container-lg {
    max-width: 100%;
  }

  .col-md-8 {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

@media (min-width: 1440px) {
  .container,
  .container-sm,
  .container-md,
  .container-lg {
    max-width: 1140px;
  }
}

@media (max-width: 1024px) {
  .container {
    width: 100%;
    max-width: 100%;
    padding-left: 15px;
  }

  .blog-grid-container {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "post-grid post-grid ";
    margin-top: 40px;
  }

  .right-bar {
    display: none;
  }

  .newsletter img.newsletter-logo {
    max-height: 50px;
    float: left;
  }
}

@media (max-width: 768px) {
  .posttitle {
    font-size: 2.188em;
  }

  .blog-grid-container {
    grid-template-columns: 1fr;
    grid-template-areas: "post-grid";
  }

  .newsletter {
    span.h4 {
      font-size: 1.25em;
    }

    .mc-field-group input[type="email"] {
      padding: 4px 10px;
      min-width: 200px;
    }

    .mc-field-group input[type="submit"] {
      padding: 4px 10px;
    }
  }

  .post-grid {
    grid-template-columns: 1fr;
    grid-template-areas: "post-grid";
  }
}

@media (max-width: 600px) {
  .nav-icon,
  .sidebar,
  .share,
  .wrap-search {
    position: relative;
  }

  .container {
    padding-left: 15px;
  }

  .site-content {
    padding-top: 20px;
  }

  .bd-search {
    visibility: visible;
    opacity: 1;
    transition: opacity 0.5s linear;
  }

  .wrap-search {
    right: 25px;
    display: flex;
    left: unset;
    z-index: 0;
  }

  .share {
    display: flex;
    align-items: center;
  }

  .share ul li {
    display: inline-block;
    margin: 10px 15px !important;
  }

  #menu ul {
    padding: 80px 30px 80px 30px;
  }

  .related-posts {
    h2 {
      font-size: 1.4rem;
    }
  }
}

@media (max-width: 480px) {
  .posttitle {
    font-size: 1.375em;
    text-align: center;
  }

  h2 {
    font-size: 1.25em;
  }

  h3 {
    font-size: 1.125em;
  }

  .newsletter {
    .mc-field-group input[type="email"] {
      min-width: 170px;
      width: 170px;
    }
  }

  .article-post,
  .entry-header,
  .content-post,
  .comments,
  .rating,
  .authorbox {
    padding-left: 0;
    padding-right: 0;
  }

  blockquote {
    margin-left: 15px;
    font-size: 1.125em;
  }

  .article-post {
    font-size: 0.938em;
  }

  .wrap-search {
    .icon-search-solid {
      display: none;
    }

    .form-control {
      height: 35px;
    }
  }
}
