@charset "UTF-8";

@keyframes plyr-progress {
  to {
    background-position: 25px 0;
    background-position: var(--plyr-progress-loading-size, 25px) 0;
  }
}

@keyframes plyr-popup {
  0% {
    opacity: .5;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes plyr-fade-in {
  0% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.plyr {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  align-items: center;
  direction: ltr;
  display: flex;
  flex-direction: column;
  font-family: inherit;
  font-family: var(--plyr-font-family, inherit);
  font-variant-numeric: tabular-nums;
  font-weight: 400;
  font-weight: var(--plyr-font-weight-regular, 400);
  line-height: 1.7;
  line-height: var(--plyr-line-height, 1.7);
  max-width: 100%;
  min-width: 200px;
  position: relative;
  text-shadow: none;
  transition: box-shadow .3s ease;
  z-index: 0;
 


  audio, iframe, video {
    display: block;
    height: 100%;
    width: 100%;
  }

  button {
    font: inherit;
    line-height: inherit;
    width: auto;
  }

  &:focus {
    outline: 0;
  }
}

.plyr--full-ui {
  box-sizing: border-box;

  *, :after, :before {
    box-sizing: inherit;
  }

  a, button, input, label {
    touch-action: manipulation;
  }
}

.plyr__badge {
  background: #4a5464;
  background: var(--plyr-badge-background, #4a5464);
  border-radius: 2px;
  border-radius: var(--plyr-badge-border-radius, 2px);
  color: #fff;
  color: var(--plyr-badge-text-color, #fff);
  font-size: 9px;
  font-size: var(--plyr-font-size-badge, 9px);
  line-height: 1;
  padding: 3px 4px;
}

.plyr--full-ui ::-webkit-media-text-track-container {
  display: none;
}

.plyr__captions {
  animation: plyr-fade-in .3s ease;
  bottom: 0;
  display: none;
  font-size: 13px;
  font-size: var(--plyr-font-size-small, 13px);
  left: 0;
  padding: 10px;
  padding: var(--plyr-control-spacing, 10px);
  position: absolute;
  text-align: center;
  transition: transform .4s ease-in-out;
  width: 100%;

  span:empty {
    display: none;
  }
}

@media (min-width: 480px) {
  .plyr__captions {
    font-size: 15px;
    font-size: var(--plyr-font-size-base, 15px);
    padding: 20px;
    padding: calc(var(--plyr-control-spacing,10px)*2);
  }
}

@media (min-width: 768px) {
  .plyr__captions {
    font-size: 18px;
    font-size: var(--plyr-font-size-large, 18px);
  }
}

.plyr--captions-active .plyr__captions {
  display: block;
}

.plyr:not(.plyr--hide-controls) .plyr__controls:not(:empty) ~ .plyr__captions {
  transform: translateY(-40px);
  transform: translateY(calc(var(--plyr-control-spacing,10px)*-4));
}

.plyr__caption {
  background: #000c;
  background: var(--plyr-captions-background, #000c);
  border-radius: 2px;
  -webkit-box-decoration-break: clone;
  box-decoration-break: clone;
  color: #fff;
  color: var(--plyr-captions-text-color, #fff);
  line-height: 185%;
  padding: .2em .5em;
  white-space: pre-wrap;

  div {
    display: inline;
  }
}

.plyr__control {
  background: #0000;
  border: 0;
  border-radius: 4px;
  border-radius: var(--plyr-control-radius, 4px);
  color: inherit;
  cursor: pointer;
  flex-shrink: 0;
  overflow: visible;
  padding: 7px;
  padding: calc(var(--plyr-control-spacing,10px)*.7);
  position: relative;
  transition: all .3s ease;

  svg {
    fill: currentColor;
    display: block;
    height: 18px;
    height: var(--plyr-control-icon-size, 18px);
    pointer-events: none;
    width: 18px;
    width: var(--plyr-control-icon-size, 18px);
  }

  &:focus {
    outline: 0;
  }

  &:focus-visible {
    outline: 2px dashed #00b2ff;
    outline: 2px dashed var(--plyr-focus-visible-color, var(--plyr-color-main, var(--plyr-color-main, #00b2ff)));
    outline-offset: 2px;
  }
}

a.plyr__control {
  text-decoration: none;
}

.plyr__control {
  &.plyr__control--pressed {
    .icon--not-pressed, .label--not-pressed {
      display: none;
    }
  }

  &:not(.plyr__control--pressed) {
    .icon--pressed, .label--pressed {
      display: none;
    }
  }
}

a.plyr__control {
  &:after, &:before {
    display: none;
  }
}

.plyr--full-ui ::-webkit-media-controls {
  display: none;
}

.plyr__controls {
  align-items: center;
  display: flex;
  justify-content: flex-end;
  text-align: center;

  .plyr__progress__container {
    flex: 1;
    min-width: 0;
  }

  .plyr__controls__item {
    margin-left: 2.5px;
    margin-left: calc(var(--plyr-control-spacing,10px)/4);

    &:first-child {
      margin-left: 0;
      margin-right: auto;
    }

    &.plyr__progress__container {
      padding-left: 2.5px;
      padding-left: calc(var(--plyr-control-spacing,10px)/4);
    }

    &.plyr__time {
      padding: 0 5px;
      padding: 0 calc(var(--plyr-control-spacing,10px)/2);
    }

    &.plyr__progress__container:first-child {
      padding-left: 0;
    }

    &.plyr__time {
      + .plyr__time, &:first-child {
        padding-left: 0;
      }
    }
  }
}

.plyr {
  [data-plyr=airplay], [data-plyr=captions], [data-plyr=fullscreen], [data-plyr=pip] {
    display: none;
  }
}

.plyr__controls:empty {
  display: none;
}

.plyr--airplay-supported [data-plyr=airplay], .plyr--captions-enabled [data-plyr=captions], .plyr--fullscreen-enabled [data-plyr=fullscreen], .plyr--pip-supported [data-plyr=pip] {
  display: inline-block;
}

.plyr__menu {
  display: flex;
  position: relative;

  .plyr__control {
    svg {
      transition: transform .3s ease;
    }

    &[aria-expanded=true] {
      svg {
        transform: rotate(90deg);
      }

      .plyr__tooltip {
        display: none;
      }
    }
  }
}

.plyr__menu__container {
  animation: plyr-popup .2s ease;
  background: #ffffffe6;
  background: var(--plyr-menu-background, #ffffffe6);
  border-radius: 8px;
  border-radius: var(--plyr-menu-radius, 8px);
  bottom: 100%;
  box-shadow: 0 1px 2px #00000026;
  box-shadow: var(--plyr-menu-shadow, 0 1px 2px #00000026);
  color: #4a5464;
  color: var(--plyr-menu-color, #4a5464);
  font-size: 15px;
  font-size: var(--plyr-font-size-base, 15px);
  margin-bottom: 10px;
  position: absolute;
  right: -3px;
  text-align: left;
  white-space: nowrap;
  z-index: 3;

  > div {
    overflow: hidden;
    transition: height 0.35s cubic-bezier(0.4, 0, 0.2, 1), width 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  }

  &:after {
    border: 4px solid #0000;
    border-top-color: #ffffffe6;
    border: var(--plyr-menu-arrow-size, 4px) solid #0000;
    border-top-color: var(--plyr-menu-background, #ffffffe6);
    content: "";
    height: 0;
    position: absolute;
    right: 14px;
    right: calc(var(--plyr-control-icon-size,18px)/2 + var(--plyr-control-spacing,10px)*.7 - var(--plyr-menu-arrow-size,4px)/2);
    top: 100%;
    width: 0;
  }

  [role=menu] {
    padding: 7px;
    padding: calc(var(--plyr-control-spacing,10px)*.7);
  }

  [role=menuitem], [role=menuitemradio] {
    margin-top: 2px;
  }

  [role=menuitem]:first-child, [role=menuitemradio]:first-child {
    margin-top: 0;
  }

  .plyr__control {
    align-items: center;
    color: #4a5464;
    color: var(--plyr-menu-color, #4a5464);
    display: flex;
    font-size: 13px;
    font-size: var(--plyr-font-size-menu, var(--plyr-font-size-small, 13px));
    padding: 4.66667px 10.5px;
    padding: calc(var(--plyr-control-spacing,10px)*.7/1.5) calc(var(--plyr-control-spacing,10px)*.7*1.5);
    -webkit-user-select: none;
    user-select: none;
    width: 100%;

    > span {
      align-items: inherit;
      display: flex;
      width: 100%;
    }

    &:after {
      border: 4px solid #0000;
      border: var(--plyr-menu-item-arrow-size, 4px) solid #0000;
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  .plyr__control--forward {
    padding-right: 28px;
    padding-right: calc(var(--plyr-control-spacing,10px)*.7*4);

    &:after {
      border-left-color: #728197;
      border-left-color: var(--plyr-menu-arrow-color, #728197);
      right: 6.5px;
      right: calc(var(--plyr-control-spacing,10px)*.7*1.5 - var(--plyr-menu-item-arrow-size,4px));
    }

    &:focus-visible:after, &:hover:after {
      border-left-color: initial;
    }
  }

  .plyr__control--back {
    font-weight: 400;
    font-weight: var(--plyr-font-weight-regular, 400);
    margin: 7px;
    margin: calc(var(--plyr-control-spacing,10px)*.7);
    margin-bottom: 3.5px;
    margin-bottom: calc(var(--plyr-control-spacing,10px)*.7/2);
    padding-left: 28px;
    padding-left: calc(var(--plyr-control-spacing,10px)*.7*4);
    position: relative;
    width: calc(100% - 14px);
    width: calc(100% - var(--plyr-control-spacing,10px)*.7*2);

    &:after {
      border-right-color: #728197;
      border-right-color: var(--plyr-menu-arrow-color, #728197);
      left: 6.5px;
      left: calc(var(--plyr-control-spacing,10px)*.7*1.5 - var(--plyr-menu-item-arrow-size,4px));
    }

    &:before {
      background: #dcdfe5;
      background: var(--plyr-menu-back-border-color, #dcdfe5);
      box-shadow: 0 1px 0 #fff;
      box-shadow: 0 1px 0 var(--plyr-menu-back-border-shadow-color, #fff);
      content: "";
      height: 1px;
      left: 0;
      margin-top: 3.5px;
      margin-top: calc(var(--plyr-control-spacing,10px)*.7/2);
      overflow: hidden;
      position: absolute;
      right: 0;
      top: 100%;
    }

    &:focus-visible:after, &:hover:after {
      border-right-color: initial;
    }
  }

  .plyr__control[role=menuitemradio] {
    padding-left: 7px;
    padding-left: calc(var(--plyr-control-spacing,10px)*.7);

    &:after {
      border-radius: 100%;
    }

    &:before {
      border-radius: 100%;
      background: #0000001a;
      content: "";
      display: block;
      flex-shrink: 0;
      height: 16px;
      margin-right: 10px;
      margin-right: var(--plyr-control-spacing, 10px);
      transition: all .3s ease;
      width: 16px;
    }

    &:after {
      background: #fff;
      border: 0;
      height: 6px;
      left: 12px;
      opacity: 0;
      top: 50%;
      transform: translateY(-50%) scale(0);
      transition: transform .3s ease,opacity .3s ease;
      width: 6px;
    }

    &[aria-checked=true] {
      &:before {
        background: #00b2ff;
        background: var(--plyr-control-toggle-checked-background, var(--plyr-color-main, var(--plyr-color-main, #00b2ff)));
      }

      &:after {
        opacity: 1;
        transform: translateY(-50%) scale(1);
      }
    }

    &:focus-visible:before, &:hover:before {
      background: #23282f1a;
    }
  }

  .plyr__menu__value {
    align-items: center;
    display: flex;
    margin-left: auto;
    margin-right: -5px;
    margin-right: calc(var(--plyr-control-spacing,10px)*.7*-1 - -2px);
    overflow: hidden;
    padding-left: 24.5px;
    padding-left: calc(var(--plyr-control-spacing,10px)*.7*3.5);
    pointer-events: none;
  }
}

.plyr--full-ui input[type=range] {
  -webkit-appearance: none;
  appearance: none;
  background: #0000;
  border: 0;
  border-radius: 26px;
  border-radius: calc(var(--plyr-range-thumb-height,13px)*2);
  color: #00b2ff;
  color: var(--plyr-range-fill-background, var(--plyr-color-main, var(--plyr-color-main, #00b2ff)));
  display: block;
  height: 19px;
  height: calc(var(--plyr-range-thumb-active-shadow-width,3px)*2 + var(--plyr-range-thumb-height,13px));
  margin: 0;
  min-width: 0;
  padding: 0;
  transition: box-shadow .3s ease;
  width: 100%;

  &::-webkit-slider-runnable-track {
    background: #0000;
    background-image: linear-gradient(90deg, currentColor 0, #0000 0);
    background-image: linear-gradient(to right, currentColor var(--value, 0), #0000 var(--value, 0));
    border: 0;
    border-radius: 2.5px;
    border-radius: calc(var(--plyr-range-track-height,5px)/2);
    height: 5px;
    height: var(--plyr-range-track-height, 5px);
    -webkit-transition: box-shadow .3s ease;
    transition: box-shadow .3s ease;
    -webkit-user-select: none;
    user-select: none;
  }

  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    background: #fff;
    background: var(--plyr-range-thumb-background, #fff);
    border: 0;
    border-radius: 100%;
    box-shadow: 0 1px 1px #23282f26,0 0 0 1px #23282f33;
    box-shadow: var(--plyr-range-thumb-shadow, 0 1px 1px #23282f26, 0 0 0 1px #23282f33);
    height: 13px;
    height: var(--plyr-range-thumb-height, 13px);
    margin-top: -4px;
    margin-top: calc((var(--plyr-range-thumb-height,13px) - var(--plyr-range-track-height,5px))/2*-1);
    position: relative;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    width: 13px;
    width: var(--plyr-range-thumb-height, 13px);
  }

  &::-moz-range-track {
    background: #0000;
    border: 0;
    border-radius: 2.5px;
    border-radius: calc(var(--plyr-range-track-height,5px)/2);
    height: 5px;
    height: var(--plyr-range-track-height, 5px);
    -moz-transition: box-shadow .3s ease;
    transition: box-shadow .3s ease;
    user-select: none;
  }

  &::-moz-range-thumb {
    background: #fff;
    background: var(--plyr-range-thumb-background, #fff);
    border: 0;
    border-radius: 100%;
    box-shadow: 0 1px 1px #23282f26,0 0 0 1px #23282f33;
    box-shadow: var(--plyr-range-thumb-shadow, 0 1px 1px #23282f26, 0 0 0 1px #23282f33);
    height: 13px;
    height: var(--plyr-range-thumb-height, 13px);
    position: relative;
    -moz-transition: all .2s ease;
    transition: all .2s ease;
    width: 13px;
    width: var(--plyr-range-thumb-height, 13px);
  }

  &::-moz-range-progress {
    background: currentColor;
    border-radius: 2.5px;
    border-radius: calc(var(--plyr-range-track-height,5px)/2);
    height: 5px;
    height: var(--plyr-range-track-height, 5px);
  }

  &::-ms-track {
    color: #0000;
  }

  &::-ms-fill-upper, &::-ms-track {
    background: #0000;
    border: 0;
    border-radius: 2.5px;
    border-radius: calc(var(--plyr-range-track-height,5px)/2);
    height: 5px;
    height: var(--plyr-range-track-height, 5px);
    -ms-transition: box-shadow .3s ease;
    transition: box-shadow .3s ease;
    user-select: none;
  }

  &::-ms-fill-lower {
    background: #0000;
    background: currentColor;
    border: 0;
    border-radius: 2.5px;
    border-radius: calc(var(--plyr-range-track-height,5px)/2);
    height: 5px;
    height: var(--plyr-range-track-height, 5px);
    -ms-transition: box-shadow .3s ease;
    transition: box-shadow .3s ease;
    user-select: none;
  }

  &::-ms-thumb {
    background: #fff;
    background: var(--plyr-range-thumb-background, #fff);
    border: 0;
    border-radius: 100%;
    box-shadow: 0 1px 1px #23282f26,0 0 0 1px #23282f33;
    box-shadow: var(--plyr-range-thumb-shadow, 0 1px 1px #23282f26, 0 0 0 1px #23282f33);
    height: 13px;
    height: var(--plyr-range-thumb-height, 13px);
    margin-top: 0;
    position: relative;
    -ms-transition: all .2s ease;
    transition: all .2s ease;
    width: 13px;
    width: var(--plyr-range-thumb-height, 13px);
  }

  &::-ms-tooltip {
    display: none;
  }

  &::-moz-focus-outer {
    border: 0;
  }

  &:focus {
    outline: 0;
  }

  &:focus-visible {
    &::-webkit-slider-runnable-track, &::-moz-range-track, &::-ms-track {
      outline: 2px dashed #00b2ff;
      outline: 2px dashed var(--plyr-focus-visible-color, var(--plyr-color-main, var(--plyr-color-main, #00b2ff)));
      outline-offset: 2px;
    }
  }
}

.plyr__poster {
  background-color: #000;
  background-color: var(--plyr-video-background, var(--plyr-video-background, #000));
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  transition: opacity .2s ease;
  width: 100%;
  z-index: 1;
}

.plyr--stopped.plyr__poster-enabled .plyr__poster {
  opacity: 1;
}

.plyr--youtube.plyr--paused.plyr__poster-enabled:not(.plyr--stopped) .plyr__poster {
  display: none;
}

.plyr__time {
  font-size: 13px;
  font-size: var(--plyr-font-size-time, var(--plyr-font-size-small, 13px));

  + .plyr__time:before {
    content: "⁄";
    margin-right: 10px;
    margin-right: var(--plyr-control-spacing, 10px);
  }
}

@media (max-width: 767px) {
  .plyr__time + .plyr__time {
    display: none;
  }
}

.plyr__tooltip {
  background: #fff;
  background: var(--plyr-tooltip-background, #fff);
  border-radius: 5px;
  border-radius: var(--plyr-tooltip-radius, 5px);
  bottom: 100%;
  box-shadow: 0 1px 2px #00000026;
  box-shadow: var(--plyr-tooltip-shadow, 0 1px 2px #00000026);
  color: #4a5464;
  color: var(--plyr-tooltip-color, #4a5464);
  font-size: 13px;
  font-size: var(--plyr-font-size-small, 13px);
  font-weight: 400;
  font-weight: var(--plyr-font-weight-regular, 400);
  left: 50%;
  line-height: 1.3;
  margin-bottom: 10px;
  margin-bottom: calc(var(--plyr-control-spacing,10px)/2*2);
  opacity: 0;
  padding: 5px 7.5px;
  padding: calc(var(--plyr-control-spacing,10px)/2) calc(var(--plyr-control-spacing,10px)/2*1.5);
  pointer-events: none;
  position: absolute;
  transform: translate(-50%, 10px) scale(0.8);
  transform-origin: 50% 100%;
  transition: transform .2s ease .1s,opacity .2s ease .1s;
  white-space: nowrap;
  z-index: 2;

  &:before {
    border-left: 4px solid #0000;
    border-left: var(--plyr-tooltip-arrow-size, 4px) solid #0000;
    border-right: 4px solid #0000;
    border-right: var(--plyr-tooltip-arrow-size, 4px) solid #0000;
    border-top: 4px solid #fff;
    border-top: var(--plyr-tooltip-arrow-size, 4px) solid var(--plyr-tooltip-background, #fff);
    bottom: -4px;
    bottom: calc(var(--plyr-tooltip-arrow-size,4px)*-1);
    content: "";
    height: 0;
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
    width: 0;
    z-index: 2;
  }
}

.plyr .plyr__control {
  &:focus-visible .plyr__tooltip, &:hover .plyr__tooltip {
    opacity: 1;
    transform: translate(-50%) scale(1);
  }
}

.plyr__tooltip--visible {
  opacity: 1;
  transform: translate(-50%) scale(1);
}

.plyr .plyr__control:hover .plyr__tooltip {
  z-index: 3;
}

.plyr__controls > .plyr__control {
  &:first-child {
    .plyr__tooltip {
      left: 0;
      transform: translateY(10px) scale(0.8);
      transform-origin: 0 100%;

      &:before {
        left: 16px;
        left: calc(var(--plyr-control-icon-size,18px)/2 + var(--plyr-control-spacing,10px)*.7);
      }
    }

    + .plyr__control .plyr__tooltip {
      left: 0;
      transform: translateY(10px) scale(0.8);
      transform-origin: 0 100%;
    }

    + .plyr__control .plyr__tooltip:before {
      left: 16px;
      left: calc(var(--plyr-control-icon-size,18px)/2 + var(--plyr-control-spacing,10px)*.7);
    }
  }

  &:last-child .plyr__tooltip {
    left: auto;
    right: 0;
    transform: translateY(10px) scale(0.8);
    transform-origin: 100% 100%;

    &:before {
      left: auto;
      right: 16px;
      right: calc(var(--plyr-control-icon-size,18px)/2 + var(--plyr-control-spacing,10px)*.7);
      transform: translateX(50%);
    }
  }

  &:first-child {
    .plyr__tooltip--visible {
      transform: translate(0) scale(1);
    }

    + .plyr__control {
      .plyr__tooltip--visible, &:focus-visible .plyr__tooltip, &:hover .plyr__tooltip {
        transform: translate(0) scale(1);
      }
    }

    &:focus-visible .plyr__tooltip, &:hover .plyr__tooltip {
