    margin-right: -6.5px;
    margin-right: calc(var(--plyr-range-thumb-height,13px)*-.5);
    width: calc(100% + 13px);
    width: calc(100% + var(--plyr-range-thumb-height,13px));
  }
}

.plyr__progress__buffer {
  margin-left: -6.5px;
  margin-left: calc(var(--plyr-range-thumb-height,13px)*-.5);
  margin-right: -6.5px;
  margin-right: calc(var(--plyr-range-thumb-height,13px)*-.5);
  width: calc(100% + 13px);
  width: calc(100% + var(--plyr-range-thumb-height,13px));
}

.plyr__progress {
  input[type=range] {
